import React, { useState, useEffect } from 'react';

const ImageSlides = ({ links }) => {
  let id = 0;
  const images = links.map((link) => {
    return (
      <img
        style={{ maxWidth: '300px', maxHeight: '300px', margin: 'auto' }}
        key={id++}
        src={link}
        className='my-row-1'
        alt='SpaceX rocket'
      />
    );
  });

  const getImage = (index) => {
    return images[index];
  };

  const [el, setEl] = useState();

  const [slideIndex, setSlideIndex] = useState(0);

  const changeImage = (direction) => {
    setSlideIndex((direction + slideIndex + images.length) % images.length);
    setEl(getImage(slideIndex));
  };

  useEffect(() => {
    setEl(getImage(0));
    // eslint-disable-next-line
  }, []);

  return (
    <div className='my-row-2-temp'>
      {el}
      <div className='my-col-2-temp my-row-2'>
        <button
          className='btn btn-secondary my-col-1'
          onClick={() => {
            changeImage(-1);
          }}
        >
          Previous Image
        </button>
        <button
          className='btn btn-secondary my-col-2'
          onClick={() => {
            changeImage(+1);
          }}
        >
          Next Image
        </button>
      </div>
    </div>
  );
};

export default ImageSlides;
