import React, { Fragment } from 'react';
import { gql, useQuery } from '@apollo/client';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import Spinner from './Spinner';
import Video from './Video';
import ImageSlides from './ImageSlides';

const LAUNCH_QUERY = gql`
  query LaunchQuery($flight_number: Int!) {
    launch(flight_number: $flight_number) {
      flight_number
      mission_name
      launch_year
      launch_success
      launch_date_local
      rocket {
        rocket_id
        rocket_name
        rocket_type
      }
      links {
        flickr_images
        youtube_id
        wikipedia
      }
    }
  }
`;

const Launch = (props) => {
  let { flight_number } = props.match.params;
  flight_number = parseInt(flight_number);

  const { loading, error, data } = useQuery(LAUNCH_QUERY, {
    variables: { flight_number },
  });

  if (loading) return <Spinner />;
  if (error) console.log(error);

  if (!data) {
    return (
      <Fragment>
        <h1 className='text-danger my-3'>No data found</h1>
        <Link to='/' className='btn btn-outline-secondary'>
          Back
        </Link>
      </Fragment>
    );
  }

  const {
    mission_name,
    launch_year,
    launch_success,
    rocket,
    links,
  } = data.launch;

  const { rocket_id, rocket_name, rocket_type } = rocket;

  const { flickr_images, youtube_id, wikipedia } = links;
  if (flickr_images) console.log('will create image slide');
  console.log(links);
  return (
    <div className='my-3'>
      <h1 className='display-4 my-3'>
        <span style={{ color: '#ffffff' }}>
          Mission:{' '}
          <span
            className={classNames({
              'text-success': launch_success,
              'text-danger': !launch_success,
            })}
          >
            {' '}
            {mission_name}{' '}
          </span>
        </span>
      </h1>
      <h4 style={{ color: '#ffffff' }} className='mb-3'>
        Launch Details
      </h4>
      <ul className='list-group'>
        <li className='list-group-item'>Flight Number: {flight_number}</li>
        <li className='list-group-item'>Launch Year: {launch_year}</li>
        <li className='list-group-item'>
          Launch Successful:{' '}
          <span
            className={classNames({
              'text-success': launch_success,
              'text-danger': !launch_success,
            })}
          >
            {launch_success ? 'Yes' : 'No'}
          </span>{' '}
        </li>
      </ul>
      <h4 style={{ color: '#ffffff' }} className='my-3'>
        Rocket Details
      </h4>
      <ul className='list-group'>
        <li className='list-group-item'>Rocket ID: {rocket_id}</li>
        <li className='list-group-item'>Rocket Name: {rocket_name}</li>
        <li className='list-group-item'>Rocket Type: {rocket_type}</li>
      </ul>

      {(youtube_id || wikipedia || flickr_images) && (
        <h4 className='my-3'>Miscellaneous</h4>
      )}
      <div className='bg-light px-3 py-3 my-col-2'>
        <div className='my-col-1'>
          {youtube_id && (
            <div className=''>
              <Video src={youtube_id} />
            </div>
          )}
          {wikipedia && (
            <div className=''>
              <a
                className='btn btn-secondary'
                href={wikipedia}
                target='__blank'
              >
                Wikipedia Page
              </a>
            </div>
          )}
        </div>
        {flickr_images && (
          <div className='my-col-2'>
            <ImageSlides links={flickr_images} />
          </div>
        )}
      </div>

      <Link
        style={{ color: '#ffffff' }}
        to='/'
        className='btn btn-outline-secondary my-2'
      >
        Back
      </Link>
    </div>
  );
};

export default Launch;
